@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;500;700&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  color: #333333;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1rem;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: #333333;
}

img {
  max-width: 100%;
  vertical-align: bottom;
}

.container {
  margin-left: 300px;
}
@media (max-width: 960px) {
  .container {
    margin-left: 0;
  }
}

.wrapper {
  max-width: 900px;
  margin: 0 auto;
  padding: 80px 20px;
}
@media (max-width: 960px) {
  .wrapper {
    padding: 60px 20px;
  }
}

.logo {
  line-height: 1px;
  width: 200px;
  margin-bottom: 30px;
}
@media (max-width: 960px) {
  .logo {
    width: 180px;
    margin-bottom: 0;
    position: relative;
    top: 10px;
    left: 14px;
  }
}
.logo a {
  display: block;
}

.sec-title {
  font-size: 1.75rem;
  text-align: center;
  margin-bottom: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 960px) {
  .sec-title {
    font-size: 1.25rem;
    margin-bottom: 30px;
  }
}
.sec-title::before, .sec-title::after {
  content: "";
  width: 28%;
  border-top: 1px solid;
}
@media (max-width: 960px) {
  .sec-title::before, .sec-title::after {
    width: 18%;
  }
}

/*-------------------------------------------
to-top トップに戻るボタン
-------------------------------------------*/
#to-top {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #333333;
  border: 1px solid #ffffff;
  position: fixed;
  bottom: 25px;
  right: 25px;
  transition: 1s;
}
#to-top::after {
  content: "";
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-bottom: 7px solid #ffffff;
  position: relative;
  left: 17px;
  bottom: 3px;
}

/*-------------------------------------------
header
-------------------------------------------*/
#header {
  padding: 40px 50px 30px 45px;
  width: 300px;
  height: auto;
  top: 0;
  overflow-y: auto;
  position: fixed;
}
@media (max-width: 960px) {
  #header {
    z-index: 10;
    padding: 0;
    width: 100%;
    height: 72px;
  }
}
#header #navigation li {
  font-size: 0.875rem;
  padding: 8px 0;
}
@media (max-width: 960px) {
  #header #navigation li {
    padding: 10px 0;
  }
}
#header #navigation .nav-menu {
  margin-bottom: 60px;
}
@media (max-width: 960px) {
  #header #navigation .nav-menu {
    margin-bottom: 30px;
  }
}

@media (max-width: 960px) {
  #navigation {
    width: 300px;
    background: #ffffff;
    padding: 25px;
    position: fixed;
    top: 0;
    left: -300px;
    bottom: 0;
    z-index: 20;
    overflow-y: auto;
    opacity: 0;
    transition: 0.5s;
  }

  #hamburger {
    width: 30px;
    height: 30px;
    cursor: pointer;
    position: fixed;
    top: 22px;
    right: 18px;
    z-index: 20;
    transition: 0.5s;
  }
  #hamburger span {
    width: 30px;
    height: 2px;
    background: #333333;
    display: block;
    border-radius: 4px;
    position: absolute;
    left: 0;
    transition: 0.5s;
  }
  #hamburger span:nth-child(1) {
    top: 4px;
  }
  #hamburger span:nth-child(2) {
    top: 14px;
  }
  #hamburger span:nth-child(3) {
    bottom: 4px;
  }
}
/*-------------------------------------------
open, mask
-------------------------------------------*/
@media (max-width: 960px) {
  .open #navigation {
    left: 0;
    opacity: 1;
  }
  .open #hamburger span {
    background-color: #ffffff;
  }
  .open #hamburger span:nth-child(1) {
    transform: translateY(10px) rotate(-315deg);
  }
  .open #hamburger span:nth-child(2) {
    opacity: 0;
  }
  .open #hamburger span:nth-child(3) {
    transform: translateY(-10px) rotate(315deg);
  }
  .open #mask {
    width: 100%;
    height: 100%;
    background-color: #000;
    cursor: pointer;
    display: block;
    opacity: 0.5;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
  }

  #mask {
    display: none;
    transition: 0.5s;
  }
}
/*-------------------------------------------
video
-------------------------------------------*/
#video {
  width: 100%;
  height: 100vh;
}
@media (max-width: 960px) {
  #video {
    height: 80vh;
  }
}
#video .bg-video {
  top: -220px;
  position: fixed;
  z-index: -100;
}
@media (max-width: 960px) {
  #video .bg-video {
    height: 520px;
    top: 72px;
    left: 0;
    right: 0;
    margin: 0 auto;
  }
}

/*-------------------------------------------
project
-------------------------------------------*/
#project {
  color: #ffffff;
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
}
#project ul {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
@media (max-width: 960px) {
  #project ul {
    flex-direction: column;
  }
}
#project ul li {
  width: 47%;
  border: 1px solid #ffffff;
  margin-bottom: 50px;
  position: relative;
}
@media (max-width: 960px) {
  #project ul li {
    width: 100%;
    margin-bottom: 30px;
  }
}
#project ul li:last-child, #project ul li:nth-last-child(2) {
  margin-bottom: 0;
}
@media (max-width: 960px) {
  #project ul li:nth-last-child(2) {
    margin-bottom: 30px;
  }
}
#project ul li a {
  display: block;
}
#project ul li a::after {
  content: "";
  box-shadow: 0 -30px 15px -1px #2e2e2e inset;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
#project ul li a .name {
  color: #ffffff;
  position: absolute;
  font-size: 0.75rem;
  bottom: 4px;
  left: 7px;
  z-index: 5;
}

/*-------------------------------------------
feature
-------------------------------------------*/
#feature {
  color: #ffffff;
  background: rgba(0, 0, 0, 0.8);
  padding-bottom: 80px;
}
@media (max-width: 960px) {
  #feature {
    padding-bottom: 60px;
  }
}
#feature .list {
  display: flex;
  justify-content: space-between;
}
#feature .list li {
  width: 30%;
  text-align: center;
  font-size: 0.875rem;
}
#feature .list a {
  border: 1px solid #ffffff;
  border-radius: 50%;
  width: 100%;
  padding: calc(50% - 11px) 0;
  display: block;
  color: #ffffff;
  margin-bottom: 15px;
  text-align: center;
  position: relative;
  overflow: hidden;
}
#feature .list a:hover::before {
  left: 0;
}
#feature .list a::before {
  content: "";
  width: 100%;
  height: 100%;
  background: #ffffff;
  position: absolute;
  top: 0;
  left: -100%;
  opacity: 0.3;
  transition: 0.5s;
}
#feature .list .title {
  font-weight: bold;
  font-size: 1rem;
  margin-bottom: 5px;
}

/*-------------------------------------------
flow
-------------------------------------------*/
#flow {
  background: #ffffff;
  padding-top: 40px;
}
@media (max-width: 960px) {
  #flow {
    padding-top: 0;
  }
}
#flow .inner {
  display: flex;
  margin-top: 60px;
}
@media (max-width: 960px) {
  #flow .inner {
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
  }
}
#flow .inner .figure {
  position: relative;
}
@media (max-width: 960px) {
  #flow .inner .figure {
    margin-bottom: 80px;
  }
}
#flow .inner .figure::before {
  content: "";
  width: 50px;
  height: 600px;
  background: #fde000;
  position: absolute;
  top: 0;
  left: 75px;
}
@media (max-width: 960px) {
  #flow .inner .figure::before {
    left: 0;
    right: 0;
    margin: 0 auto;
  }
}
#flow .inner .figure li {
  width: 200px;
  height: 80px;
  background: #333333;
  line-height: 80px;
  color: #ffffff;
  position: relative;
  text-align: center;
  margin-bottom: 60px;
}
@media (max-width: 960px) {
  #flow .inner .figure li {
    width: calc(100vw - 80px);
  }
}
#flow .inner .figure li::before {
  content: "";
  border-top: 30px solid #333333;
  border-left: 100px solid transparent;
  border-right: 100px solid transparent;
  position: absolute;
  top: 80px;
  left: 0;
  right: 0;
  margin: 0 auto;
}
@media (max-width: 960px) {
  #flow .inner .figure li::before {
    border-left: calc(50vw - 40px) solid transparent;
    border-right: calc(50vw - 40px) solid transparent;
  }
}
#flow .inner .figure li:last-child {
  margin-bottom: 0;
}
#flow .inner .description {
  margin-left: 10%;
}
@media (max-width: 960px) {
  #flow .inner .description {
    margin-left: 50px;
  }
}
#flow .inner .description dt {
  font-size: 1.25rem;
  border-bottom: 1px solid #333333;
  padding-bottom: 8px;
  margin-bottom: 10px;
  position: relative;
}
#flow .inner .description dt span {
  font-size: 1rem;
  background: #333333;
  color: #ffffff;
  width: 35px;
  height: 35px;
  line-height: 35px;
  border-radius: 50%;
  display: block;
  text-align: center;
  position: absolute;
  left: -45px;
  top: 0;
}
#flow .inner .description dd {
  margin-bottom: 43px;
}
#flow .inner .description dd:last-child {
  margin-bottom: 0;
}

/*-------------------------------------------
contact
-------------------------------------------*/
#contact {
  background: #ffffff;
  padding-bottom: 80px;
}
@media (max-width: 960px) {
  #contact {
    padding-bottom: 60px;
  }
}
#contact .inner {
  display: flex;
  justify-content: space-between;
  margin-top: 60px;
}
@media (max-width: 960px) {
  #contact .inner {
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
  }
}
#contact .inner .item {
  display: block;
  width: 30%;
  text-align: center;
  border: 1px solid #333333;
  position: relative;
  padding: 50px 0;
  font-size: 0.875rem;
  transition: 0.1s;
}
@media (max-width: 960px) {
  #contact .inner .item {
    width: 60%;
    margin: 20px 0 0 0;
  }
  #contact .inner .item:first-child {
    margin-top: 0;
  }
}
#contact .inner .item:hover {
  outline: 3px solid #333333;
}
#contact .inner .item::before {
  content: "";
  border-top: 20px solid transparent;
  border-right: 20px solid #333333;
  position: absolute;
  right: 4px;
  bottom: 4px;
}
#contact .inner .item img {
  margin-bottom: 5px;
}

/*-------------------------------------------
footer
-------------------------------------------*/
#footer {
  background: #333333;
  color: #ffffff;
  padding: 20px 0;
}
#footer .menu {
  display: flex;
  justify-content: center;
  margin: 30px 0 60px 0;
}
@media (max-width: 960px) {
  #footer .menu {
    flex-wrap: wrap;
  }
}
#footer .menu li {
  font-size: 0.875rem;
  border-right: 1px solid #ffffff;
  padding: 0 20px;
}
@media (max-width: 960px) {
  #footer .menu li {
    margin-bottom: 10px;
  }
}
#footer .menu li:last-child {
  border-right: none;
}
#footer .menu li a {
  color: #ffffff;
}
#footer .copyright {
  font-size: 0.65rem;
  text-align: center;
}